<template>
  <div class="foodHome">
    <foodHeaderNav title="个人中心" eventShow="false"></foodHeaderNav>
    <div class="body">
      <div class="list">
        <div class="item">
          <span>站点：</span>
          <span>{{workStationCode}}</span>
        </div>
        <div class="item">
          <span>工号：</span>
          <span>{{waiter}}</span>
        </div>
        <!-- <div class="item">开单</div>
        <div class="item">已结</div>
        <div class="item">未结</div>
        <div class="item">消费</div>
        <div class="item">付款</div>-->
      </div>
      <div class="sign-out" @click="gotoLogin()">退 出</div>
    </div>
    <footTabs></footTabs>
  </div>
</template>
<script>
import footTabs from "../common/footTabs";
import foodHeaderNav from "../common/foodHeaderNav";
export default {
  components: {
    footTabs,
    foodHeaderNav
  },
  data() {
    return {
      workStationCode: null,
      waiter: null
    };
  },
  created() {
    this.waiter = localStorage.waiter;
    let foodUserInfo = JSON.parse(localStorage.foodUserInfo);
    this.workStationCode = foodUserInfo.workStationCode;
    console.log("waiter:", this.waiter);
    console.log("workStationCode:", this.workStationCode);
  },
  methods: {
    gotoLogin() {
      this.$router.push("/foodLogin");
    }
  }
};
</script>
<style lang="less" scoped>
.foodHome {
  .body {
    .list {
      background-color: #fff;
      padding-left: 0.4rem;
      margin-bottom: 1.067rem;
      .item:last-child {
        border-bottom: none;
      }
      .item {
        height: 1.253rem;
        line-height: 1.253rem;
        text-align: left;
        font-size: 0.373rem;
        color: #000000;
        border-bottom: 1px solid #eee;
      }
    }
    .sign-out {
      height: 50;
      line-height: 1.33rem;
      background-color: #fff;
      font-size: 0.373rem;
      color: #333333;
    }
  }
}
</style>



